
 /* MEGAMENU */
/*======================================*/
& when (@megamenu = true){
	.mega-dropdown{
		.open&{
			position: static;
			> .mega-dropdown-toggle{
				&:before, &:after{
					content: "";
					border-left: 7px solid transparent;
					border-right: 7px solid transparent;
					display: block;
					height: 0;
					position: absolute;
					width: 0
				}
				&:before{
					border-bottom: 7px solid darken(@border-color-base, 10%);
					margin: -8px 0 0 0;
					bottom: -10px;
				}
				&:after{
					border-bottom: 7px solid #fff;
					margin: -7px 0 0 0;
					bottom: -11px;
					z-index: 10000
				}
			}
		}
		.navbar-top-links > & > .dropdown-menu&-menu{
			left: 18px;
			right: 18px;
			max-width: 1250px;
		}
		.dropdown-menu&-menu{
			padding: 15px 0;
			&:after{
				content: '';
				display: table;
				clear: both;
			}
			.list-unstyled{
				.dropdown-header{
					font-size: 1.2em;
					font-weight: @font-semibold;
					padding: 10px 4px;
				}
				li{
					a{
						display: block;
						padding: 4px;
						color:rgba(0,0,0,.6);
						background-color: transparent;
						&:not(.disabled-link):hover{
							color:rgba(0,0,0,1);
							background-color: rgba(0,0,0,0.065);
						}
					}
				}

			}
		}
	}
}










/* WIDGET */
/*======================================*/
.widget-header{
	padding: 15px 15px 50px 15px;
	min-height: 125px;
	position: relative;
	overflow: hidden
}
.widget-bg{
	position: absolute;
	top: 0;
	left: 0;
	min-width: 100%;
	min-height: 100%
}

.widget-title{
	position: relative
}

.widget-body{
	padding: 50px 15px 15px;
	position: relative
}
.widget-img{
	position: absolute;
	width: 64px;
	height: 64px;
	left: 50%;
	margin-left: -32px;
	top: -32px;
}







 /* WIDGET - CHAT */
/*======================================*/
& when (@email = true){
	.speech{
		position: relative;
		background: #b7dcfe;
		color: #317787;
		display: inline-block;
		border-radius: @border-radius-base;
		padding: 12px 20px;
		.media-heading{
			font-size: 1.2em;
			color: #317787;
			display: block;
			border-bottom: 1px solid rgba(0,0,0,.1);
			margin-bottom: 10px;
			padding-bottom: 5px;
			font-weight: 300;
			&:hover{
				text-decoration: underline
			}
		}
		&-time{
			margin-top: 20px;
			margin-bottom: 0;
			font-size:.8em;
			font-weight: 300
		}
		&-right{
			text-align: right;
			> .speech{
				background: #ffda87;
				color: #a07617;
				text-align: right;
				.media-heading{
					color: #a07617;
				}
				&:before{
					left: auto;
					right: 0;
					border-top: 7px solid transparent;
					border-bottom: 7px solid transparent;
					border-left: 7px solid #ffdc91;
					border-right:0;
					margin: 15px -6px 0 0;
				}
			}
		}
		&:before{
			content: "";
			display: block;
			position: absolute;
			width: 0;
			height: 0;
			left:0;
			top:0;
			border-top: 7px solid transparent;
			border-bottom: 7px solid transparent;
			border-right: 7px solid #b7dcfe;
			margin: 15px 0 0 -6px;
		}
	}
}









/* TIMELINE */
/*======================================*/
& when(@timeline = true) {
	.timeline {
		position: relative;
		padding-bottom: 40px;
		//background-color: @timeline-bg;
		background-color: @timeline-bg;
		color: @timeline-color;
		&:before, &:after{
			background-color: lighten(@timeline-border-color, 12%); // #d4dfe5; //darken(@border-color-base,20%);
			bottom: 20px;
			content: "";
			display: block;
			position: absolute;
		}
		&:before {
			left: 49px;
			top: 20px;
			width: 2px;
		}
		&:after {
			left: 47px;
			width: 6px;
			height: 6px;
			border-radius: 50%
		}
	}

	.timeline-header{
		border-radius: @border-radius-base;
		clear: both;
		margin-bottom: 50px;
		margin-top: 50px;
		position: relative;
		.timeline-header-title{
			display: inline-block;
			text-align: center;
			padding: 7px 15px;
			min-width: 100px;
		}
		.timeline &:first-child{
			margin-bottom: 30px;
			margin-top: 15px;
		}
	}

	.timeline-stat{
		width: 100px;
		float: left;
		text-align: center;
		padding-bottom: 15px
	}
	.timeline-entry {
		margin-bottom: 50px;
		margin-top: 5px;
		position: relative;
		clear: both
	}
	.timeline-entry-inner {
		position: relative;
	}
	.timeline-time{
		display: inline-block;
		padding: 5px 3px 7px;
		margin-top: 3px;
		background-color: @timeline-bg;
		color: lighten(@timeline-color, 20%);
		font-size: .85em;
		max-width: 70px
	}
	.timeline-icon {
		border-radius: 50%;
		box-shadow: 0 0 0 7px @body-bg;
		display: block;
		margin: 0 auto;
		height: 46px;
		line-height: 46px;
		text-align: center;
		width: 46px;
		& img {
			width: 46px;
			height: 46px;
			border-radius: 50%;
			vertical-align: top
		}
	 	&:empty {
			height: 10px;
			width: 10px;
			margin-top:20px;
			background-color: @timeline-border-color;
		}
	}
	.timeline-label {
		background-color: @light-bg;
		border-radius: @border-radius-base;
		margin-left: 90px;
		padding: 10px;
		position: relative;
		min-height: 50px;
		border:1px solid @border-color-base;
		box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05);
		&:before, &:after{
			content: "";
			display: block;
			position: absolute;
			width: 0;
			height: 0;
			left:0;
			top:0;
		}
		&:before{
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			border-right: 10px solid darken(@border-color-base,1%);
			margin: 15px 0 0 -10px;
		}
		&:after{
			border-top: 9px solid transparent;
			border-bottom: 9px solid transparent;
			border-right: 9px solid @light-bg;
			margin: 15px 0 0 -8px;
		}
	}


	.panel {
		.timeline, .timeline-time{
			background-color: #fff;
		}
		.timeline-icon {
			box-shadow: 0 0 0 7px #fff;
		}
		.timeline-label{
			box-shadow: none;
			background-color: darken(@light-bg, 3%);
			border:1px solid darken(@border-color-base, 2.5%);
			&:before{
				border-right-color: darken(@border-color-base, 2.5%);
			}
			&:after{
				border-right-color: darken(@light-bg, 3%);
			}
		}
	}

	@media (min-width: 768px){
		.two-column.timeline{
			text-align: center;
			&:before{
				left: 50%;
			}
			&:after{
				left: 50%;
				margin-left: -2px;
			}
			.timeline-entry{
				width: 50%;
				text-align: left
			}
			.timeline-stat{
				margin-left: -50px
			}
			.timeline-entry:nth-child(odd){
				float: right;
				.timeline-label{
					margin-left: 40px
				}
			}
			.timeline-header{
				text-align: center;
			}
			.timeline-entry:nth-child(even){
				float: left;
				.timeline-stat{
					left: 100%;
					position: relative;
					margin-left: -50px;
				}
				.timeline-label{
					left: -90px;
					margin-right: -40px;
					&:before, &:after{
						left:auto;
						right:0;
						border-right: 0 solid transparent;
					}
					&:before{
						border-top: 10px solid transparent;
						border-bottom: 10px solid transparent;
						border-left: 10px solid darken(@border-color-base,1%);
						margin: 15px -10px 0 0;
					}
					&:after{
						border-top: 9px solid transparent;
						border-bottom: 9px solid transparent;
						border-left: 9px solid @light-bg;
						margin: 15px -8px 0 0;
					}
				}
			}
		}
	}
}





/* TAGS */
/*======================================*/
.tag:not(.label) {
	background-color: #fff;
	padding:6px 12px;
	border-radius: 2px;
	border:1px solid darken(@gray-bg, 7%);
	font-size: @font-size-base;
	line-height: 1.42857;
	vertical-align: middle;

	-webkit-transition: all .15s;
	transition: all .15s;
}
.tag.tag-lg, .btn-group-lg > .tag {
	padding: 10px 16px;
}
.tag.tag-sm, .btn-group-sm > .tag {
	padding: 5px 10px;
}

.tag.tag-xs, .btn-group-xs > .tag {
	padding: 1px 5px;
}




& when (@email = true){

	/* EMAIL */
	/*======================================*/

	.mail-nav > li{
		> a{
			border-radius: 0;
			&:hover, &:focus{
				background-color: transparent;
			}
		}
		&.active > a{
			&, &:hover, &:focus{
				background-color: rgba(0,0,0,0.07);
				color: inherit;
				font-weight: @font-semibold
			}
		}
	}
	.mail-list{
		list-style: none;
		padding: 0;
		> li{
			position: relative;
			padding:15px 15px;
			border-radius: 1px;
			&:after{
				content: '';
				display: table;
				clear: both
			}
		}
	}
	.mail-list-unread a{font-weight:@font-semibold}
	.mail-control{
		float: left;
		width: 40px
	}
	.mail-star{
		position: absolute;
		top:47px;
		margin-left: 1px
	}
	.mail-from{
		float: left;
		width: 105px;
	}
	.mail-subject{
		margin:2.3em 0 0 40px;
		.label{
			margin-right:.5em;
			vertical-align: middle;
			line-height: 2;
		}
	}
	.mail-from a, .mail-subject a{
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.mail-attach-icon{
		width: 35px;
		float: right;
		text-align: center;
		min-height: 1.5em
	}
	.mail-time{
		float: right;
		color: @text-muted;
		font-size: 85%;
		text-align: right
	}
	.mail-toggle.navbar-toggle{
		display: block
	}

	@media (min-width: 992px){
		.mail-nav{
			width: @aside-width;
		}
		.mail-menu{
			display: block !important;
		}
		.mail-control, .mail-star{
			float: left;
			width: 50px
		}
		.mail-from{
			width: 175px;
		}
		.mail-attach-icon{
			width: 35px;
			float: right;
			text-align: right;
			min-height: 1.5em
		}
		.mail-time{
			width: 75px;
		}
		.mail-subject{
			overflow: hidden;
			margin:  0 20px 0 0;
		}
	}

	@media (min-width: 470px){
		.mail-star{
			position: static;
			float: left;
			width: 35px
		}
		.mail-from{
			width: 175px;
			margin-right: 20px;
		}
		.mail-subject{
			display: block;
			padding-right: 50px
		}
	}


	/* MAIL ICON - (REQUIRE FONTAWESOME) */
	/*======================================*/
	.mail-attach .mail-attach-icon:before {
		content: "\f0c6";
	}
	.mail-star > a:before {
		content: "\f006"
	}
	.mail-starred .mail-star > a:before {
		content: "\f005"
	}
	.mail-attach .mail-attach-icon:before,
	.mail-star > a:before,
	.mail-stared > a:before{
		font: normal normal normal 20px/1 FontAwesome;
	}





	/* COLORS */
	/*======================================*/
	.mail-list{
		> li{
			&:nth-child(odd){
				background-color: @email-list-odd;
			}
			&:hover{
				background-color: @email-list-hover
			}
			&.highlight{
