			}
		}
		a{
			&, &:focus{
				display: block;
				color: @body-color;
			}
		}
	}
	.mail-star {
		> a, > a:focus{
			color: @email-unstared-color
		}
		.mail-starred &{
			> a{
				color:@email-stared-color
			}
		}
		.mail-list & > a:hover{
