		overflow: hidden
	}
	.mail-attach-btn{
		float: right
	}
	@media (min-width: 1200px){
		.mail-attach-label{
			float: left
		}
	}
	.mail-message-reply{
		min-height: 150px;
		border:1px solid @border-color-base;
		border-radius: @border-radius-base;
		padding:10px 15px;
		cursor: pointer
	}

}









/* FORM WIZARD */
/*======================================*/
& when (@form-wizard = true){
	.wz-nav-off > li a {
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		cursor: default !important;
	}

	.wz-icon-inline li > a .icon-wrap{
		display: inline-block;
		line-height: 1;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		color: inherit
	}



	// Black & white icons
	.wz-icon-bw li {
		> a .icon-wrap, > a p{
			.transition(all .5s)
		}
		&.active ~ li > a .icon-wrap{
			color: @body-color;
			background-color: rgba(0,0,0,.17);
			.transition(all .5s)
		}
		&:not(.active) > a p {
			color: @body-color !important;
			.transition(all .5s)
		}
	}


	/* FORM WIZARD : Classic */
	/*======================================*/
	& when(@include-form-wizard-classic = true){
		.wz-classic {
			margin:0;
			padding:0;
			list-style: none;
			display: block;
			position: relative;
			li{
				.transition(all .5s);
				> a {
					color:inherit;
					display: block;
					text-align: center;
					padding:20px 0;
					.icon-wrap{
						display: inline-block;
						line-height: 1;
						text-align: center;
						white-space: nowrap;
						vertical-align: middle;
						color: inherit
					}
				}
			}
			&:after{
				content: '';
				display: table;
				clear: both
			}
			.active ~ li {
				color: inherit;
				background-color : inherit;
				a {
					opacity: .5;
				}
			}
		}

	}

	& when (@include-form-wizard-steps = true) {

		/* FORM WIZARD : Steps */
		/*======================================*/
		.wz-heading{
			position: relative;
			.progress {
				position: absolute;
				left: 0;
				right: 0;
				top: 50%;
				background-color: transparent
			}
			&.wz-w-label .progress {
				margin-top: -.5em;
			}
		}

		.wz-steps {
			margin: 0;
			padding: 20px 0;
			list-style: none;
			display: block;
			position: relative;
			&:after {
				& when (@include-form-wizard-classic = true){
					&:extend(.wz-classic:after);
				}
				& when (@include-form-wizard-classic = false) {
					content: '';
					display: table;
					clear: both
				}
			}
			li{
				& when (@include-form-wizard-classic = true){
					&:extend(.wz-classic li);
				}

				& when (@include-form-wizard-classic = false) {
					.transition(all, .5s);
				}

				> a{
					color: inherit;
					display: block;
					text-align: center;
					padding: 0;
					.icon-wrap {
						display: inline-block;
						line-height: 1;
						text-align: center;
						white-space: nowrap;
						vertical-align: middle;
					}
				}
				.wz-desc {
					opacity: 0;
					.transition(opacity .5s);
				}
				&:not(.active) .wz-icon {
					display: none
				}
			}
		}

		.wz-steps .active{
			~ li {
				color: inherit;
				a{
					opacity: .5;
				}
				.wz-icon {
					display: inline-block
				}
			}
			.wz-icon-done, ~ li .wz-icon-done {
				display: none
			}
			.wz-desc {
				opacity: 1;
				.transition(opacity .5s);
			}
		}
	}
}










& when(@nifty-scrollTop = true){

	/* SCROLL TO TOP BUTTON */
	/*======================================*/
	#scroll-top {
		background-color: #404449;
		color:#fff;
		cursor: pointer;
		position: fixed;
		bottom: 55px;
		font-size: 20px;
		right: 5px;
		border-radius: 2px;
		opacity: 0;
		z-index: 999;
		.transition(opacity .3s);
		&.in{
			opacity: .5;
			transition: opacity .3s;
			&:hover {
				opacity: 1;
				.transition(opacity .3s)
			}
		}
	}
}














& when(@nifty-overlay = true){

	/* NIFTY OVERLAY */
	/*======================================*/
	.panel-overlay-wrap{
		position: relative
	}
	.panel-overlay{
		position: absolute;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(255,255,255,0.75);
		text-align: center;
		z-index: 795;
		&:before{
			content: "";
			display: inline-block;
			height: 100%;
			width:1px;
			vertical-align: middle;
			margin-left: -5px;
		}
	}
	.panel-overlay-title{
		margin: 10px 0 5px;
	}
	.panel-overlay-icon{
		display: inline-block;
		vertical-align: middle;
	}
	.panel-overlay-content{
		display: inline-block;
		vertical-align:middle
	}
}






& when (@nifty-language-selector = true){

	/* NIFTY LANGUAGE SELECTOR */
	/*======================================*/
	.lang-flag{
		display: inline-block;
		padding: 3px 5px 3px 0;
	}
	.lang-id{
		font-weight: @font-semibold;
	}
	.lang-id, .lang-name{
		display: none;
		vertical-align: middle
	}
	.lang-selector .lang-id{
		display: inline-block
	}
	.lang-selector + .dropdown-menu .lang-name{
		display: inline-block;
	}
	.lang-selector + .dropdown-menu.with-arrow{
		margin-top: 10px
	}
	.lang-selector + .dropdown-menu a{
		padding: 10px
	}
	.lang-selector + .dropdown-menu .active{
		display:none;
	}
	@media (min-width: 992px){
		.lang-id, .lang-selector .lang-id{
			display: none
		}
		.lang-name{
			display: inline-block;
		}
	}
}












& when(@nifty-noty = true) {

	/* NIFTY NOTIFICATION */
	/*======================================*/
	.alert-wrap {
		margin: 0;
		max-height: 0;
		overflow: hidden;
		padding: 0;
		.transition(max-height .7s linear);
		> .alert {
			margin: 0;
			box-shadow: inset 0 0 0 1px rgba(0, 0, 0, .1);
			border-radius: 0;
			text-align: left;
			> .media{
				margin: 0;
				> .media-body{
					min-width: 150px;
					width: auto;
					vertical-align: middle
				}
			}
			#page-alert > &{
				box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, .04);
			}
		}
		&.in {
			max-height: 500px;
			.transition(max-height 1s linear);
		}
	}



	.floating-container .animated.alert-wrap {
		overflow: visible
	}
	.floating-container .animated.alert-wrap > .alert {
		border-radius: 2px
	}



	.alert-message {
		font-size: .9em;
		margin-bottom: 1px;
		padding-right: 25px;
	}
	.alert-title {
		font-size: 1.12em;
		&:extend(.alert-message);
		&:empty{
			display: none
		}

	}

	.alert-variant-icon(@bg, @color) {
		.alert-icon{
			background-color: screen(@bg, #606060);
			color: @color
		}
	}

	.alert-primary{.alert-variant-icon(@primary-bg, @primary-color)}
	.alert-info{.alert-variant-icon(@info-bg, @info-color)}
	.alert-success{.alert-variant-icon(@success-bg, @success-color)}
