/* FULL CALENDAR */
/* ====================================== */
& when(@full-calendar = true){
	#container{
		.fc-unthemed th,
		.fc-unthemed td,
		.fc-unthemed hr,
		.fc-unthemed thead,
		.fc-unthemed tbody,
		.fc-unthemed .fc-row,
		.fc-unthemed .fc-popover {
			border-color: @border-color-base;
		}


		.fc-row.fc-widget-header table{
			th{
				height: 30px;
				vertical-align: middle;
				background-color: @primary-bg;
				color: @primary-color;
			}
		}
		.fc-toolbar{
			margin-bottom: 0;
			h2{
				font-weight: @font-semibold;
				font-size: 20px;
			}
			.fc-left, .fc-center, .fc-right{
				margin-bottom: 10px;
				float: none
			}
			> * > *, .fc-button-group > *{
				float: none;
			}
		}


		.fc-button, .fc button{
			cursor: pointer;
			padding:6px 12px;
			border-radius: @border-radius-base;
			font-size: @font-size-base;
			line-height: 1.42857;
			height: auto;
			vertical-align: middle;
			box-shadow: none;
			text-shadow: none;
			.transition(all .25s);
			text-transform: capitalize;
			background-image: none;
			background-color: darken(screen(@light-bg, #000),1.5%);
			border: 1px solid darken(@gray-bg, 7%);
			color: @light-color;
			&.fc-state-active{
				background-color: @primary-bg;
				color: @primary-color;
				border-color: @primary-bg;
				box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.2);
			}
		}

		.fc-button:not(.disabled):not(:disabled):active,
		.fc button:not(.disabled):not(:disabled).active {
			box-shadow: inset 0 2px 1px rgba(0, 0, 0, 0.2);
		}

		.fc-state-default.fc-corner-left {
			border-bottom-left-radius: @border-radius-base;
			border-top-left-radius: @border-radius-base;
		}


		.fc-state-default.fc-corner-right {
			border-bottom-right-radius: @border-radius-base;
			border-top-right-radius: @border-radius-base;
		}

		.fc-unthemed .fc-today {
			background-color: #fbfccc;
		}

		.fc-variant(@color, @background){
			&:not(.fc-list), &.fc-list:before{
				background-color: @background;
				border-color: @background;
				color: @color;
			}
		}

		.fc-popover.fc-more-popover .fc-event{
			padding: 5px 12px;
			font-size: 1em;
			margin-bottom: 5px;
		}
		.fc-view-container .fc-event{
			padding: 2px 5px;
			margin-bottom: 0;
			font-size: .85em;
		}
		.fc-event{
			background-color: @primary-bg;
			cursor: grab;
			color: @primary-color;
			border:1px solid @primary-bg;
			padding: 7px 12px;
			font-size: 1em;
			margin-bottom: 10px;
			border-radius: @border-radius-base;
			width: auto !important;


			&.ui-draggable-dragging{
				cursor: grabbing;
			}
			&.fc-list{
				background-color: #f5f5f5;
				color: inherit;
				border-color: transparent;
				position: relative;
				padding-left: 30px;
				&:before{
					content: '';
					display: block;
					background-color: @primary-bg;
					color: @primary-color;
					width: 8px;
					height: 8px;
					position: absolute;
					top: 50%;
					left: 5px;
					margin-top: -4px;
				}
				&.ui-draggable-dragging{
					box-shadow: 0 0 5px rgba(0,0,0,0.1)
				}
			}

			&.info, &[data-class="info"]{
				.fc-variant(@info-color, @info-bg);
			}
			&.success, &[data-class="success"]{
				.fc-variant(@success-color, @success-bg);
			}
			&.warning, &[data-class="warning"]{
				.fc-variant(@warning-color, @warning-bg);
			}
			&.danger, &[data-class="danger"]{
				.fc-variant(@danger-color, @danger-bg);
			}
			&.mint, &[data-class="mint"]{
				.fc-variant(@mint-color, @mint-bg);
			}
			&.purple, &[data-class="purple"]{
				.fc-variant(@purple-color, @purple-bg);
			}
			&.pink, &[data-class="pink"]{
				.fc-variant(@pink-color, @pink-bg);
			}
			&.dark, &[data-class="dark"]{
				.fc-variant(@dark-color, @dark-bg);
			}
		}

		.fc-highlight {
			opacity: .5;
		}

	}
}
@media (min-width: 640px){
	#container .fc-toolbar{
		.fc-left{
			float: left
		}
		.fc-right{
			float: right;
		}
		> * > *, .fc-button-group > *{
			float: left;
		}
	}
}
