

/* BOOTSTRAP'S COMPONENTS */
/*======================================*/
.collapse{
	display: none
}

.jumbotron{
	padding:30px;
	background-color: #f7f7f8
}

.navbar-toggle .icon-bar{
	background-color: #aaa
}

.canvas-responsive{
	max-width: 100%
}



a{
	text-decoration: none;
	color:@link-color;
	outline: 0;
	&:hover,
	&:focus{
		text-decoration: none;
		color: @link-hover-color;
		outline: 0 !important
	}
}
button, button:focus{
	outline: 0 !important
}
code{
	background-color: #e7e3f2;
	color: #ff0000;
	padding:2px 7px;
	border-radius: 2px;
	font-size: 97%
}
kbd {
	border-radius: 2px;
	box-shadow: none;
}
label{
	font-weight: normal
}
legend{
	padding:10px;
	font-size: 18px;
	font-weight: @font-semibold;
	border-color: #eee;
}
mark, .mark{
	background-color: #ffe3a2;
	color:#563c00;
	padding:.1em
}




/* HORIZONTAL RULE */
/*======================================*/
hr{
	border-color: rgba(0,0,0,0.05);
}
.hr{
	&-wide{
		margin-left: 0 - (@grid-gutter-width/2);
		margin-right: 0 - (@grid-gutter-width/2);
	}
	&-xs{
		margin:5px 0
	}
	&-sm{
		margin:10px 0
	}
}



[class^="col-"]:not(.pad-no){
	padding-left: (@grid-gutter-width/2);
	padding-right: (@grid-gutter-width/2);
}



/* MEDIA */
/*======================================*/
.media-block{
	.media-left{
		display: block;
		float: left;
	}
	.media-right{
		float: right;
	}
	.media-body{
		display: block;
		overflow: hidden;
		width: auto
	}
}
.middle{
	.media-left,
	.media-right,
	.media-body{
		vertical-align: middle
	}
}





/* THUMBNAIL */
/*======================================*/
.thumbnail{
	border-radius: @border-radius-base;
	border-color: @border-color-base;
}




/* TABLE */
/*======================================*/
& when (@table = true) {
	#container{
		.table{
			th{
				font-size: 1.05em;
				font-weight: @font-semibold;
				border-bottom: 2px solid rgba(0,0,0,0.14);
			}
			td{
				border-top: 1px solid rgba(0,0,0,0.05);
			}
			&.table-vcenter{
				th, td{
					vertical-align: middle
				}
			}
			.min-width{
				width: 1%;
				white-space: nowrap;
				padding-left: 15px !important;
				padding-right: 15px !important
			}
		}
		.table-bordered{
			td, th{
				border-color: rgba(0,0,0,0.11);
			}
		}
		.table-striped > tbody > tr:nth-child(2n+1){
			background-color: lighten(@gray-bg, 6%);
		}
		.table-hover > tbody > tr:hover{
			background-color: lighten(@gray-bg,4%);
		}
	}
}



 /* FORM CONTROL  */
/*======================================*/
& when (@form-control = true) {
	.form-control{
		font-size: @font-size-base;
		//height: ceil((@font-size-base * 1.42857) + 14);
		height: 100%;
		border-radius: @border-radius-base;
		box-shadow: none;
		border: 1px solid @border-color-base;
		transition-duration: .5s;
		&:focus{
			border-color:@state-active-bg;
			box-shadow: none;
			transition-duration: .5s;
			&-feedback{
				z-index: 10
			}
		}
	}


	.has-error .form-control,
	.has-warning .form-control,
	.has-success .form-control{
		box-shadow: none !important
	}

	.fa.form-control-feedback{
		line-height: 25px
	}

	.input-group-addon{
		border:1px solid #e1e5ea;
		background-color: transparent;
		border-radius: @border-radius-base;
		min-width: 45px;
	}
}



/* NAV PILLS */
/*======================================*/
& when (@nav-pills = true) {
	.nav{
		&-pills{
			> li{
				> a{
					border-radius: @border-radius-base;
				}
			}
			>.active{
				> a{
					&, &:hover, &:focus{
						background-color: @primary-bg;
					}
				}
			}
		}
		&-tabs{
			>li{
				>a{
					border-radius: @border-radius-base @border-radius-base 0 0
				}
			}
		}
	}
}










/* LIST GROUP */
/*======================================*/
& when (@list-group = true) {
	.list-group{
		&.bg-trans{
			.list-group-item{
				&:not(.active):not(.disabled){
					background-color: transparent;
					border-color: transparent;
					color: inherit
				}
				.disabled{
					opacity: .5
				}
			}
			a.list-group-item:hover:not(.active){
				background-color: rgba(0,0,0,0.05);
			}
		}
		&.bord-no .list-group-item{
			border-color: transparent
		}
		.list-divider{
			display: block
		}

	}



	.list-group-item{
		border-color: @border-color-base;
		&-heading {
			margin-top:5px;
		}
		&:first-child {
			border-top-left-radius : @border-radius-base;
			border-top-right-radius : @border-radius-base;
		}
		&:last-child {
			border-bottom-left-radius : @border-radius-base;
			border-bottom-right-radius : @border-radius-base;
		}
		.list-group-item.disabled{
			&, &:hover, &:focus{
				background-color: rgba(0,0,0,.07);
				border-color: transparent
			}
		}
		&.active{
			&, &:hover, &:focus{
				background-color: @state-active-bg;
				border-color: @state-active-bg;
				color: @state-active-color;
				.list-group-item-text{
					color: @state-active-color;
				}
			}
		}
		a&:hover, a&:focus{
			background-color: rgba(0,0,0,.05);
		}
	}




	// Mixin
	// ----------------------------------------------
	.list-variant(@color, @background){
		&, a&{
			background-color: lighten(@background,7%);
			border-color: transparent;
			//color: darken(@background,7%)
			color: @color
		}
		a&:hover, a&:focus{
			background-color: lighten(@background,10%);
			color: @color
		}
	}



	// List variant
	// ----------------------------------------------
	.list-group-item-primary{
		.list-variant(@primary-color, @primary-bg)
	}

	.list-group-item-info{
		.list-variant(@info-color, @info-bg)
	}

	.list-group-item-success{
		.list-variant(@success-color, @success-bg)
	}

	.list-group-item-warning{
		.list-variant(@warning-color, @warning-bg)
	}

	.list-group-item-danger{
		.list-variant(@danger-color, @danger-bg)
	}

	& when (@enable-mint = true) {
		.list-group-item-mint{
			.list-variant(@mint-color, @mint-bg)
		}
	}

	& when (@enable-purple = true) {
		.list-group-item-purple{
			.list-variant(@purple-color, @purple-bg)
		}
	}

	& when (@enable-pink = true) {
		.list-group-item-pink{
			.list-variant(@pink-color, @pink-bg)
		}
	}
	& when (@enable-dark = true) {
		.list-group-item-dark{
			.list-variant(@dark-color, @dark-bg)
		}
	}

}









/* LABEL */
/*======================================*/
& when (@label = true) {
	.label{
		font-weight: normal;
		border-radius: 0.1em;
		font-size: 85%;
		&:empty{
			display: inline-block;
			width: 1.5em;
			height: 1.5em;
			vertical-align: sub;
		}
		&.label-fw{
			margin-right: .5em
		}
		.labels &{
			display: inline-block;
			margin-right: 3px;
			margin-bottom: 3px
		}
		&-md{
			font-size: 100%;
		}
		&-table{
			display:inline-block;
			width: 80%;
			min-width:8ex;
			font-size:1em;
			max-width:100px;
			padding:5px;
			text-overflow: ellipsis;
			overflow: hidden
		}
	}


	// Label variant
	// ----------------------------------------------
	.label-default{
		background-color: #e3e8ee;
		color: #333
	}
	.label-primary{
		background-color: @primary-bg;
	}
	.label-info{
		background-color: @info-bg;
	}
	.label-success{
		background-color: @success-bg;
	}
	.label-warning{
		background-color: @warning-bg;
	}
	.label-danger{
		background-color: @danger-bg;
	}
	& when (@enable-mint = true) {
		.label-mint{background-color: @mint-bg}
	}
	& when (@enable-purple = true) {
		.label-purple{background-color: @purple-bg}
	}
	& when (@enable-pink = true) {
		.label-pink{background-color: @pink-bg}
	}
	& when (@enable-dark = true) {
		.label-dark{background-color: @dark-bg}
	}
}







 /* BREADCRUMB */
/*======================================*/
& when (@breadcrumb = true) {
	.breadcrumb{
		border-radius: 0;
		background-color: transparent;
		margin-bottom: 10px;
		padding: 0 20px;
		li{
			&, & a{
				font-size: .85em;
				color: @text-muted
			}

		}
		>li{
			& + li:before{
				content: "\f105";
				display: inline-block;
				font-family: FontAwesome;
				font-style: normal;
				font-weight: normal;
				line-height: 1;
			}
		}

	}
	@media (min-width: 992px){
		.breadcrumb{
			padding:0 25px;
		}
	}
}




 /* ALERT */
/*======================================*/
& when (@alert = true) {
	.alert{
		border-radius: @border-radius-base;
		border:0;
		padding: 15px 3em 15px 15px;
		position: relative;
		.alert-link{
			text-decoration: underline;
			font-weight: normal;
			&:hover{
				text-decoration: underline
			}
		}
	}


	.alert {
		.close{
			text-shadow: none;
			opacity: .3;
			position: absolute;
			left: auto;
			right: 10px;
			top:10px;
		}
		button.close{
			padding: 1px;
			border-radius: 20px;
			.transition(all, .3s);
			>span:not(.sr-only){
				display: block;
				width: 1em;
				height: 1em;
				line-height: .8em
			}
			&:hover{
				opacity: .55;
			}
			&:active{
				opacity:.3
			}
		}
	}


	// Mixin
	// ----------------------------------------------
	.alert-variant(@color, @background){
		background-color: screen(@background, #171717);
		border-color: transparent;
		color: @color;
		.close, .alert-link{
			color: @color
		}
	}


	// Alert variant
	// ----------------------------------------------
	.alert-primary{
		.alert-variant(@primary-color, @primary-bg)
	}
	.alert-info{
		.alert-variant(@info-color, @info-bg)
	}
	.alert-success{
		.alert-variant(@success-color, @success-bg)
	}
	.alert-warning{
		.alert-variant(@warning-color, @warning-bg)
	}
	.alert-danger{
		.alert-variant(@danger-color, @danger-bg)
	}

	& when (@enable-mint = true) {
		.alert-mint{.alert-variant(@mint-color, @mint-bg)}
	}
	& when (@enable-purple = true) {
		.alert-purple{.alert-variant(@purple-color, @purple-bg)}
	}
	& when (@enable-pink = true) {
		.alert-pink{.alert-variant(@pink-color, @pink-bg)}
	}
	& when (@enable-dark = true) {
		.alert-dark{.alert-variant(@dark-color, @dark-bg)}
	}

}





 /* MODAL */
/*======================================*/
& when (@modal = true) {
	.modal{
		text-align: center;
		&:before{
			content: '';
			display: inline-block;
			height: 100%;
			width: 0;
			margin-left: -1em;
			vertical-align: middle;
		}
		&.fade{
			&:not(.animated) .modal-dialog{
				opacity: 0;
				-webkit-transform : translateY(-150%);
				-ms-transform : translateY(-150%);
				transform : translateY(-150%);
				-webkit-transition: all .5s linear .5s;
				transition: all .5s linear .5s;
			}
			&.in:not(.animated) .modal-dialog{
				opacity: 1;
				-webkit-transform : translateY(0%);
				-ms-transform : translateY(0%);
				transform : translateY(0%);
				-webkit-transition: all .5s;
				transition: all .5s;
			}
		}
		.bootbox&{
			&.in:not(.fade){
				opacity: 1;
				.transition(opacity, .5s)
			}
			&:not(.fade){
				opacity: 0;
				-webkit-transition:opacity .5s linear .5s;
				transition:opacity .5s linear .5s;
			}
		}
		&-dialog{
			display: inline-block;
			vertical-align: middle;
			text-align: left;
			margin-top: -1%;
			min-width: 90%;
			&.animated{
				-webkit-animation-duration: .7s;
				animation-duration: .7s;
			}
		}
		&-header{
			padding:15px;
			position: relative;
			border:0;
			&:after{
				content: '';
				position: absolute;
				bottom: 0;
				left: 15px;
				right: 15px;
				height: 1px;
				border-bottom: 1px solid rgba(0,0,0,.1)
			}
		}
		&-footer{
			background-color: rgba(0,0,0,.025);
			color: #5c7174;
			border-color: rgba(0,0,0,.04);
			border-top-left-radius: @border-radius-base;
			border-top-right-radius: @border-radius-base;
		}
		&-title{
			font-size: 18px;
			font-weight: 300
		}
		&-body{
			min-height: 90px
		}
		&-body .close, &-header .close{
			top:15px;
			right: 10px;
			left: auto;
			position: absolute;
			background-color: transparent !important
		}
		&-content{
			box-shadow: none;
			border-radius:@border-radius-base
		}
		&-footer{
			padding: 10px 15px;
			border-bottom-left-radius: @border-radius-base;
			border-bottom-right-radius: @border-radius-base;
		}
		&-backdrop.in{
			opacity: 0.75
		}
	}



	@media (min-width: 768px){
		.modal-dialog{
			min-width: 0
		}
	}
}






 /* TOOLTIP */
/*======================================*/
& when (@tooltip = true) {
	.tooltip{
		z-index: 999999;
		&-inner{
			font-size: @font-size-base;
			border-radius: @border-radius-base;
			padding: 5px 10px;
			background-color: @tooltip-bg
		}
		&.top{
			.tooltip-arrow, &-left .tooltip-arrow, &-right .tooltip-arrow{
				border-top-color: @tooltip-bg;
			}
		}
		&.right{
			.tooltip-arrow{
				border-right-color: @tooltip-bg
			}
		}
		&.left{
			.tooltip-arrow{
				border-left-color: @tooltip-bg
			}
		}
		&.bottom{
			.tooltip-arrow, &-left .tooltip-arrow, &-right .tooltip-arrow{
				border-bottom-color: @tooltip-bg
			}
		}
	}
}






/* POPOVER */
/*======================================*/
& when (@popover = true) {
	.popover{
		font-family: @font-family-base;
		font-size: @font-size-base;
		border-radius: @border-radius-base;
		box-shadow: 0 1px 2px rgba(0, 0, 0, .1);
		&-title{
			background-color: #f5f6f7;
			color: #5c7174;
			font-weight: 300;
			border-radius: @border-radius-base @border-radius-base 0 0;
		}
		>.arrow{
			border-width: 9px;
			&:after{
				border-width: 9px;
			}
		}
		&.left{
			>.arrow{
				right: -9px;
				margin-top: -9px;
				&:after{
					bottom: -9px;
				}
			}
		}
		&.right{
			>.arrow{
				left: -9px;
				margin-top: -9px;
				&:after{
					bottom: -9px;
				}
			}
		}
		&.top{
			>.arrow{
				bottom: -9px;
				&:after{
					margin-left: -9px
				}
			}
		}
		&.bottom{
			>.arrow{
				top: -9px;
				&:after{
					margin-left: -9px
				}
			}
		}
		&-content{
			padding: 10px 15px 20px;
		}
	}
}





 /* BADGE */
/*======================================*/
& when (@badge = true) {
	#container .badge{
		font-weight: normal;
		color:#fff;

	}

	.badge:empty.badge-icon{
		display: inline-block;
		width: 0.7em;
		height: 0.7em;
		padding: 0;
		min-width: 5px;
		margin: .5em;
		border-radius: 50%;
	}
	.badge.badge-fw, &.badge:empty.badge-fw{
		margin-right: 1em
	}


	.badge-default{
		background-color: #e3e8ee;
		color: #333
	}
	.badge-primary{
		background-color:@primary-bg!important;
	}
	.badge-info{
		background-color: @info-bg!important;
	}
	.badge-success{
		background-color: @success-bg!important;
	}
	.badge-warning{
		background-color: @warning-bg!important;
	}
	.badge-danger{
		background-color: @danger-bg!important;
	}
	& when (@enable-mint = true) {
		.badge-mint{background-color: @mint-bg!important;}
	}
	& when (@enable-purple = true) {
		.badge-purple{background-color: @purple-bg!important;}
	}
	& when (@enable-pink = true) {
		.badge-pink{background-color: @pink-bg!important;}
	}
	& when (@enable-dark = true) {
		.badge-dark{background-color: @dark-bg!important;}
	}
}





 /* DROPDOWN */
/*======================================*/
& when (@dropdown = true) {
	.dropdown{
		&-header{
			padding: 5px 20px 5px 10px
		}
		&-toggle{
			> .dropdown-caret{
				padding-left: .5em
			}
			&.dropdown-toggle-icon{
				>i{
					padding: 0 .25em;
				}
			}
		}
	}



	.dropdown-menu{
		font-size: @font-size-base;
		border-radius: @border-radius-base;
		box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.15);
		margin:1px 0 0 0;
		padding:0;
		border:1px solid @border-color-base;
		&-right{
			left: auto;
			right: 0
		}
		.dropup &{
			box-shadow: 0 -2px 1px 0 rgba(0,0,0,0.15);
		}
		>li{
			>a{
				padding:5px 10px;
				&:hover{
					background-color: @state-active-bg;
					color: @state-active-color
				}
			}
		}
		&.with-arrow{
			&:before, &:after{
				content: "";
				display: block;
				position: absolute;
				width: 0;
				height: 0;
				left:0;
				top:0;
				border-left: 7px solid transparent;
				border-right: 7px solid transparent;
			}
			&:before{
				border-bottom: 7px solid darken(@border-color-base, 10%);
				margin:-7px 0 0 15px;
			}
			&:after{
				border-bottom: 7px solid #fff;
				margin:-6px 0 0 15px;
			}
		}
		&-right.dropdown-menu.with-arrow{
			&:before{
				left: auto;
				right: 0;
				margin:-7px 25px 0 0;
			}
			&:after{
				left: auto;
				right: 0;
				margin:-6px 25px 0 0;
			}
		}
	}



	// Sizes
	// ----------------------------------------------
	.dropdown-menu{
		&-sm{
			min-width: 200px;
		}
		&-md{
			min-width: 275px;
		}
		&-lg{
			min-width: 350px;
		}
	}

	.dropdown.open > .btn,
	.btn-group.open .dropdown-toggle{
		box-shadow: inset 0 3px 1px rgba(0, 0, 0, 0.3);
	}

}






 /* WELL */
/*======================================*/
& when (@well = true) {
	.well{
		background-color: rgba(0,0,0,.05);
		border-radius: @border-radius-base;
		box-shadow:none
	}

	.well-xs{
		padding:5px;
	}
}







/* PROGRESSBAR */
/*======================================*/
& when (@progressbar = true) {
	.progress{
		height: 12px;
		margin-bottom: 15px;
		border-radius: @border-radius-base;
		box-shadow: none;
		background-color: rgba(0,0,0,0.1);
		.progress-bar{
			font-size: 9px;
			line-height: 12px;
			box-shadow: none
		}
		&-light-base{
			background-color: #fff;
		}
		&-dark-base{
			background-color: rgba(255,255,255,0.2)
		}

	}

	// Mixin
	// ----------------------------------------------
	.progress-sizes(@height, @margin-btm, @progress-font-size){
		height: @height;
		margin-bottom: @margin-btm;
		.progress-bar{
			font-size: @progress-font-size;
			line-height: @height;
		}
	}


	// Sizes
	// ----------------------------------------------
	.progress-xl{.progress-sizes(30px, 20px, @font-size-base)}
	.progress-lg{.progress-sizes(20px, 20px, @font-size-base)}
	.progress-md{.progress-sizes(8px, 5px, 5px)}
	.progress-sm{.progress-sizes(4px, 5px, 0)}
	.progress-xs{.progress-sizes(2px, 10px, 0)}


	// Colors
