}




/*======================================*/
/* PAGER */
/*======================================*/
& when (@pager = true) {
	.pager{
		li{
			>a{
				&:active{
					box-shadow: inset 0 3px 1px rgba(0, 0, 0, 0.2);
				}
				&:hover,&:focus{
					background-color: @light-bg;
					border-color: @state-active-bg;
					color: @state-active-bg;
					box-shadow: inset 0 0 1px @state-active-bg;
					z-index: 2;
					.transition(border-color, .3s)
				}
			}
			> a, >span{
				border-radius: @border-radius-base;
				border-color: darken(@border-color-base,5%);
			}
		}
		&.pager-rounded{
			li{
				>a, > span{
					border-radius: 15px;
				}
			}
		}
		.disabled{
			> a, > span{
				&, &:hover, &:focus{
					opacity: .7;
					border-color: darken(@border-color-base,5%);
					box-shadow: none
				}
			}
		}
	}
}




/*======================================*/
/* PAGINATION */
/*======================================*/
& when (@pagination = true){
	.pagination{
		>li{
			>a, >span{
				color: inherit;
				border-color: darken(@border-color-base,5%);
				.transition(border-color,.3s)
			}
			a {
				&:active{
					& when (@pager = false) {
						box-shadow: inset 0 3px 1px rgba(0, 0, 0, 0.2);
					}
					& when (@pager = true) {
						&:extend(.pager li > a:active);
					}
				}
				&:hover,&:focus{
					& when (@pager = false) {
						background-color: @light-bg;
						border-color: @state-active-bg;
						color: @state-active-bg;
						box-shadow: inset 0 0 1px @state-active-bg;
						z-index: 2;
						.transition(border-color, .3s)
					}
					& when (@pager = true) {
						&:extend(.pager li > a:hover);
					}
				}
			}
			>span{
				cursor: default;
				&:hover{
					background-color: @light-bg
				}
			}
			&:first-child{
				>a, span{
					border-top-left-radius:@border-radius-base;
					border-bottom-left-radius:@border-radius-base;
				}
			}
			&:last-child{
				>a, span{
					border-top-right-radius:@border-radius-base;
					border-bottom-right-radius:@border-radius-base;
				}
			}
		}
		>.active{
			>a, >span, >a:hover, >span:hover, >a:focus, >span:focus{
				background-color:@state-active-bg;
				border-color: @state-active-bg;
			}
		}
		.disabled{
			> a, > span{
				&, &:hover, &:focus{
					& when (@pager = false) {
						opacity: .7;
						border-color: darken(@border-color-base,5%);
						box-shadow: none;
					}
					& when (@pager = true) {
						&:extend(.pager .disabled > a);
					}
				}
			}
		}
	}
}





/* CAROUSEL                             */
/*======================================*/
& when (@carousel = true){
	.carousel{
		&-control{
			&.left, &.right{
				background-image: none;
				background-repeat: no-repeat;
				color: inherit
			}
			&,&:focus{
				font-size: 1em;
				text-shadow: none;
				width: auto;
				padding: 10px;
				top: 0;
				bottom:0;
				opacity: .5;
				.transition(opacity .5s)
			}
			&.auto-hide{
				opacity: 0
			}
			&:before{
				content: '';
				display: inline-block;
				height: 100%;
				width: 0;
				vertical-align: middle
			}
			i{
				position: relative;
				top:.25em
			}
		}
		&:hover{
			.carousel-control{
				opacity: 1;
				.transition(opacity .5s);
			}
		}
		&-indicators{
			&.out{
				bottom: 0;
				+ .carousel-inner{
					padding-bottom: 30px;
				}
			}
			&.square{
				li{
					border-radius: 1px;
				}
			}
			&.dark{
				.active{
					background-color: @dark-bg;
				}
				li{
					border-color: @dark-bg;
				}
			}
		}
	}
}





/* BLOCKQUOTE */
/*======================================*/
.bq{
	&-sm{
		font-size: @font-size-base;
	}
	&-open:before{
		color: rgba(0, 0, 0, 0.3);
		content: open-quote;
		font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
		font-size: 4em;
		line-height: 0.1em;
		margin-right: 0.25em;
		vertical-align: -0.4em;
	}
}



/* FORM */
/*======================================*/
.form-horizontal{
	.control-label{
		margin-bottom: 5px;
		&.text-left{
			text-align: left
		}
	}
}





/* ACCORDION */
/*======================================*/
& when (@accordion = true){
	.panel{
		> .panel-heading + .panel-collapse > .panel-body{
			border-top: 0
		}
		&-group{
			margin-bottom: 35px;
			&.accordion{
				.panel{
					margin-bottom: 0;
					&-heading{
						border-radius: 0;
						height: auto;
						&:after{
							border: 0 !important
						}
					}
					&-heading, &-title{
						padding: 0;
					}
					&-title{
						line-height: 40px;
						a{
							display: block;
							color: inherit;
							text-transform: none;
							font-size: @font-size-base;
							font-weight: 300;
							padding:0 15px;
							&:hover,&:focus{
								color: inherit
							}
						}
					}
					&:first-child{
						&, .panel-heading{
							border-top-left-radius: @border-radius-base;
							border-top-right-radius: @border-radius-base;
						}
					}
					&:last-child{
						border-bottom-left-radius: @border-radius-base;
						border-bottom-right-radius: @border-radius-base;
						overflow: hidden;
					}
				}
			}
		}
	}
}




/* TAB */
/*======================================*/
& when (@tabs = true){
	.tab{
		&-base{
			margin-bottom: 35px;
			.tab-content{
				background-color: @light-bg;
				box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05);
				border-bottom-left-radius: 2px;
				border-bottom-right-radius: 2px;
				padding: 15px;
				.tab-footer{
					background-color: lighten(@body-bg,3%);
					color: @body-color;
					border-color: lighten(@body-bg,1%);
					position: relative;
					margin: 0 -15px -15px -15px;
					padding:10px 15px
				}
			}
			.nav-tabs{
				border:0;
				>li:not(.active){
					>a{
						background-color: rgba(255, 255, 255, 0.35);
						opacity: .7;
						.transition(opacity,.3s);
						&:hover{
							opacity: .9;
							background-color: rgba(255, 255, 255, 0.55);
							border-color: @state-active-bg;
							border-bottom-color: transparent;
							.transition(opacity,.3s);
						}
					}
				}
				> .active{
					> a, a:hover, > a:focus{
						border-color: transparent
					}
				}
				&.tabs-right{
					text-align: right;
					> li {
						float: none;
						display: inline-block;
						margin-right: -2px;
					}
				}
			}
		}
		&-footer:after{
			content:'';
			display: table;
			clear: both
		}

	}

	.nav-tabs{
		li a{
			border-radius: @border-radius-base @border-radius-base 0 0
		}
		&.tab-right{
			text-align: right;
			>li{
				display: inline-block;
				text-align: left;
				float: none;
				>a {
					margin-right: 0
				}
			}
		}
		.label, .badge{
			margin-left: 4px
		}
	}


	.tab-stacked{
		&-left, &-right{
			display: table;
			height: 100%;
			width: 100%;
			& .nav-tabs{
				>li{
					float: none;
					margin: 0;
					>a{
						margin: 0 0 2px;
					}
					&:last-child{
						> a{
							margin-bottom: 0;
						}
					}
				}
			}
			& .nav-tabs, & .tab-content{
				display: table-cell;
				vertical-align: top
			}
			.tab-content{
				overflow: hidden;
			}
		}
		&-left{
			.nav-tabs{
				width:1%;
				border:0;
				>li{
					a{
						border-right-color: transparent;
						border-radius: @border-radius-base 0 0 @border-radius-base
					}
				}
				>.active{
					>a{
						&:hover, &:focus{
							border-right-color: transparent;
						}
					}
				}

			}
			&.tab-base{
				.nav-tabs{
					>li:not(.active){
						a{
							&:hover{
								border-color: @state-active-bg;
								border-right-color: transparent;
							}
						}
					}
				}
			}
			.tab-content{
				border-left-color: transparent;
				border-radius: 0 @border-radius-base @border-radius-base 0;
			}
		}
		&-right{
			.nav-tabs{
				width:1%;
				border:0;
				>li{
					a{
						border-left-color: transparent;
						border-radius: 0 @border-radius-base @border-radius-base 0
					}
				}
				>.active{
					>a{
						&:hover, &:focus{
							border-left-color: transparent;
						}
					}
				}
			}
			&.tab-base{
				.nav-tabs{
					>li:not(.active){
						a{
							&:hover{
								border-color: @state-active-bg;
								border-left-color: transparent;
							}
						}
					}
				}
			}
			.tab-content{
				border-right-color: transparent;
				border-radius: @border-radius-base 0 0 @border-radius-base
			}
		}
	}
}







