	.btn-lg, .btn-group-sm > .btn,
	.btn-lg, .btn-group-xs > .btn{
		border-radius: @border-radius-base
	}

	.btn-group-vertical {
		> .btn:first-child:not(:last-child){
			border-top-left-radius:@border-radius-base;
			border-top-right-radius:@border-radius-base
		}
		> .btn:last-child:not(:first-child){
			border-bottom-left-radius:@border-radius-base;
			border-bottom-right-radius:@border-radius-base
		}
	}





	/* Button Circle */
	/*---------------------------------*/
	& when (@include-btn-circle) {
		.btn-circle{
			padding:7px;
			border-radius:50%
		}
		.btn-lg.btn-circle {
			padding: 17px;
		}
		.btn-sm.btn-circle {
			padding: 7px;
		}
		.btn-xs.btn-circle {
			padding: 4px;
		}
		.btn-icon.btn-circle:before{
			display: block;
			width: 1.4em;
			height: 1.4em;
			line-height: 1.4
		}
	}



	/* Button Rounded */
	/*---------------------------------*/
	& when (@include-btn-rounded = true){
		.btn-rounded{
			border-radius: 17px;
			overflow: hidden
		}
		.btn-lg.btn-rounded{
			border-radius: 30px
		}
		.btn-sm.btn-rounded{
			border-radius: 15px
		}
		.btn-xs.btn-rounded{
			border-radius: 10px
		}
		.btn-group.btn-rounded{
			overflow: hidden
		}
	}


	/* Button Labeled */
	/*---------------------------------*/
	& when (@include-btn-labeled = true){
		.btn-labeled,
		.btn-labeled.fa{
			overflow: hidden;
		}
		.btn-labeled:not(.btn-block):not(.form-icon) {
			font-family: inherit;
			font-size: @font-size-base;
			line-height: 1.42857;
			padding-bottom: 0;
			padding-top: 0;
		}
		.btn-block.btn-labeled:not(.form-icon) {
			font-family: inherit;
			font-size: @font-size-base;
			line-height: 1.42857;
		}
		.btn-block.btn-labeled:not(.form-icon):before {
			float: left;
			margin-top:-7px;
			margin-bottom: -7px;
		}
		.btn-labeled .btn-label {
			background-color: rgba(0, 0, 0, .05);
			display: inline-block;
			margin-left: -12px;
			margin-right: 6px;
			padding: 6px 12px;
			line-height: 1.42857;
		}

		.btn-labeled:before{
			background-color: rgba(0, 0, 0, .05);
			display: inline-block;
			margin-left: -12px;
			margin-right: 6px;
			padding: 6px 12px
		}
		.btn-labeled.fa:before,
		.btn-labeled .fa:before{
			font-family: fontAwesome;
		}
		.btn-default.btn-labeled:before,
		.btn-default .btn-label{
			background-color: rgba(0,0,0,.05);
			color:inherit
		}
		.btn-lg.btn-labeled{
			font-size: 18px;
			line-height: 1.33
		}
		.btn-sm.btn-labeled{
			font-size: 12px;
			line-height: 1.5
		}
		.btn-xs.btn-labeled{
			font-size: 12px;
			line-height: 1.5
		}

		.btn-lg.btn-labeled:before,
		.btn-lg .btn-label{
			margin-left: -16px;
			margin-right: 10px;
			padding:10px 16px
		}

		.btn-lg.btn-block.btn-labeled:not(.form-icon):before {
			margin-top:-10px;
			margin-bottom: -10px;
		}

		.btn-sm.btn-labeled:before,
		.btn-sm .btn-label{
			margin-left: -10px;
			margin-right: 5px;
			padding:5px 10px
		}

		.btn-sm.btn-block.btn-labeled:not(.form-icon):before {
			margin-top:-5px;
			margin-bottom: -5px;
		}

		.btn-xs.btn-labeled:before,
		.btn-xs .btn-label{
			margin-left: -5px;
			margin-right: 1px;
			padding:1px 5px
		}

		.btn-xs.btn-block.btn-labeled:not(.form-icon):before {
			margin-top:-1px;
			margin-bottom: -1px;
		}


		.btn-labeled.icon-2x:before,
		.btn-labeled .btn-label.icon-2x:before{
			vertical-align: -.15em
		}

		.btn-labeled.icon-3x:before,
		.btn-labeled .btn-label.icon-3x:before{
			vertical-align: -.18em
		}

		.btn-labeled.icon-4x:before,
		.btn-labeled .btn-label.icon-4x:before{
			vertical-align: -.20em
		}

		.btn-labeled.icon-5x:before,
		.btn-labeled .btn-label.icon-5x:before{
			vertical-align: -.25em
		}

	}
}




/* PANELS */
/*======================================*/
& when (@panels = true){
	.panel{
		box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05);
		border-radius: @border-radius-base;
		border:0;
		margin-bottom:@grid-gutter-width;
		& when (lightness(@body-bg) >= 95%) {
			&:not(.panel-colorful){
	  			border: 1px solid darken(@body-bg, 5.5%);
			}
		}
		hr{
			border-color: rgba(0,0,0,.1);
		}
		.panel-bg-cover{
			max-height: 180px;
			overflow: hidden;
			img{
				min-width: 100%;
				min-height: 100%;
				background-size: cover
			}
		}
		&.remove{
			opacity: 0;
			.transition(opacity,.5s);
		}
		.alert{
			border-radius: 0
		}
	}


	.panel {
		&.panel-bg-img{
			position: relative;
		}
		.panel-bg-wrap {
			overflow: hidden;
			position: absolute;
			top:0;
			left: 0;
			bottom: 0;
			right: 0;
			> img{
				position: absolute;
				top:0;
				left: 0;
			}
			+ .panel-body{
				position: relative
			}
		}
	}



	.panel-media{
		box-shadow: 0 -50px 20px -10px rgba(0, 0, 0, 0.2);
		padding: 10px 15px 15px 140px;
		position: relative;
		&-img{
			position: absolute;
			width: 96px;
			height: 96px;
			left: 20px;
			top: -48px;
		}
		&-heading{
			color: #fff;
			position: absolute;
			top:-2.7em
		}
	}




	.panel .panel-heading,
	.panel > :first-child{
		border-top-left-radius: max(0px, @border-radius-base - 1px);
		border-top-right-radius: max(0px, @border-radius-base - 1px);
	}
	.panel .panel-footer,
	.panel > :last-child{
		border-bottom-left-radius: max(0px, @border-radius-base - 1px);
		border-bottom-right-radius: max(0px, @border-radius-base - 1px);
	}
	.panel-body-full{
		margin-left: -20px;
		margin-right: -20px;
	}
	.panel-body{
		padding: 25px 20px;

		.row, .form-horizontal .form-group {
			margin-left: 0 - (abs(20px - (@grid-gutter-width/2)));
			margin-right: 0 - (abs(20px - (@grid-gutter-width/2)));
		}
	}
	.panel-trans{
		border-color: transparent;
		box-shadow: none
	}
	.panel-heading{
		position: relative;
		height: @panel-heading-height;
		padding: 0
	}
	.panel-title{
		font-weight: 300;
		padding: 0 20px 0 20px;
		font-size: 1.416em;
		line-height: @panel-heading-height;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}





	.panel-default.panel-colorful{
		background-color: darken(@gray-bg,1%);
		color: @gray-color;
	}

	.panel-default .panel-heading{
		background-color: darken(@gray-bg,1%);
		border-color: lighten(@gray-bg,4%);
	}

	.panel-footer{
		background-color: lighten(@body-bg,3%);
		color: @body-color;
		border-color: lighten(@body-bg,1%);
		position: relative
	}




	/* Mixin  */
	/*---------------------------------*/
	.panel-variant(@color, @background){
		& .panel-heading,
		& .panel-footer,
		&.panel-colorful{
			background-color: @background;
			border-color: @background;
			color: @color;
		}
	}


	/* Colors */
	/*---------------------------------*/
	.panel-primary{.panel-variant(@primary-color, @primary-bg)}
	.panel-info{.panel-variant(@info-color, @info-bg)}
	.panel-success{.panel-variant(@success-color, @success-bg)}
	.panel-warning{.panel-variant(@warning-color, @warning-bg)}
	.panel-danger{.panel-variant(@danger-color, @danger-bg)}
	& when (@enable-mint = true) {
		.panel-mint{.panel-variant(@mint-color, @mint-bg)}
	}
	& when (@enable-purple = true) {
		.panel-purple{.panel-variant(@purple-color, @purple-bg)}
	}
	& when (@enable-pink = true) {
		.panel-pink{.panel-variant(@pink-color, @pink-bg)}
	}
	& when (@enable-dark = true) {
		.panel-dark{.panel-variant(@dark-color, @dark-bg)}
	}





	.panel > .panel-heading:after,
	.panel.panel-colorful > .panel-heading:after{
		content:'';
		display: block;
		position: absolute;
		height: 0;
		left: 20px;
		right: 20px;
		border-bottom: 1px solid rgba(0,0,0,.1)
	}
	.panel-colorful > .panel-heading{
		border:0
	}
	.panel-default > .panel-heading:after, .panel-primary > .panel-heading:after, .panel-info > .panel-heading:after,
	.panel-success > .panel-heading:after, .panel-warning > .panel-heading:after, .panel-danger > .panel-heading:after,
	.panel-purple > .panel-heading:after, .panel-pink > .panel-heading:after, .panel-dark > .panel-heading:after{
		display: none
	}



	/* Bordered */
	/*---------------------------------*/
	.panel-bordered-default, .panel-default.panel-bordered{
		border: 1px solid darken(@gray-bg, 15%)
	}
	.panel-bordered-primary, .panel-primary.panel-bordered{
		border: 1px solid @primary-bg
	}
	.panel-bordered-info, .panel-info.panel-bordered{
		border: 1px solid @info-bg
	}
	.panel-bordered-success, .panel-success.panel-bordered{
		border: 1px solid @success-bg
	}
	.panel-bordered-warning, .panel-warning.panel-bordered{
		border: 1px solid @warning-bg
	}
	.panel-bordered-danger,	.panel-danger.panel-bordered{
		border: 1px solid @danger-bg
	}
	& when (@enable-mint = true) {
		.panel-bordered-mint, .panel-mint.panel-bordered{
			border: 1px solid @mint-bg
		}
	}
	& when (@enable-purple = true) {
		.panel-bordered-purple, .panel-purple.panel-bordered{
			border: 1px solid @purple-bg
		}
	}
	& when (@enable-pink = true) {
		.panel-bordered-pink, .panel-pink.panel-bordered{
			border: 1px solid @pink-bg
		}
	}
	& when (@enable-dark = true) {
		.panel-bordered-dark, .panel-dark.panel-bordered{
			border: 1px solid @dark-bg
		}
	}






	/* Panel Group */
	/*---------------------------------*/
	.panel-group{
		.panel{
			border-radius: 0;
			margin-bottom: 20px
		}
		> div{
			padding-left: 0;
			padding-right: 0;
			&:first-child > .panel{
				border-top-left-radius: 2px;
				border-bottom-left-radius: 2px;
			}
			&:last-child > .panel{
				border-top-right-radius: 2px;
				border-bottom-right-radius: 2px;
			}
			+ div > .panel{
				margin-left: -1px
			}
		}
	}





	/* Panel Control */
	/*---------------------------------*/
	.panel-control{
		height: 100%;
		position: relative;
		float: right;
		padding:0 15px;
		&:before{
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
			left: -1em;
			position: relative;
		}
		> i,
		> .badge ,
		> label {
			vertical-align: middle
		}
	}






	/* Panel with tabs */
	/*---------------------------------*/
	& when (@panel-w-tabs = true){
		@max-tabs-height : 40px;
		.panel-control{
			.nav-tabs{
				& when (@panel-heading-height > @max-tabs-height){
					display: inline-block;
					height: @max-tabs-height;
					margin-top: @panel-heading-height - @max-tabs-height;
					vertical-align: bottom;
				}
				& when (@panel-heading-height <= @max-tabs-height){
					height: 100%;
					margin-top:-1px;
				}
				border:0;
				> li{
					margin-top: 1px;
					margin-right: 5px;
					height: 100%;
					> a{
						border-radius: 0;
						margin-right: 0;
						height: 100%;
						& when (@panel-heading-height > @max-tabs-height){
							line-height: @max-tabs-height;
							border-top-left-radius: @border-radius-base;
							border-top-right-radius: @border-radius-base;
						}
						& when (@panel-heading-height <= @max-tabs-height){
							line-height: @panel-heading-height;
						}
						padding: 0 15px
					}
					&:not(.active){
						background-color: transparent;
						opacity: .6;
						a{
							color:inherit;
							border-bottom: 0 !important;
							&:hover{
								background-color: rgba(0,0,0,.15);
								border-color: transparent
							}
						}
					}
				}
				> .active > a{
					z-index: 1;
				}
			}
		}


		& when (@panel-heading-height <= @max-tabs-height){
			.panel-control .nav-tabs > li:last-child.active > a{
				&, &:hover, &:focus{
					border-right-color: transparent;
					border-top-right-radius: @border-radius-base
				}
			}
		}

	}





	/* Panel with progress bar */
	/*---------------------------------*/
	& when (@panel-w-progressbar = true){
		//With Progress Bar
		.panel-control .progress{
			min-width: 150px;
			margin: 0;
			display: inline-block;
			vertical-align: middle;
		}
	}



	/* Panel with switch */
	/*---------------------------------*/
	.panel-control .switchery{
		margin-left:15px;
		vertical-align: middle
	}



	/* Panel with pager */
	/*---------------------------------*/
	& when (@panel-w-pager = true){
		.panel-control .pager{
			margin: 0;
			display: inline-block;
			vertical-align: middle;
			border-radius: 0;
		}
	}


	/* Panel with pagination */
	/*---------------------------------*/
	& when (@panel-w-pagination = true){
		.panel-control .pagination{
			margin: 0;
			border-radius: 0;
			vertical-align: middle;
			> li{
				> a, > span{
					padding: 0 10px;
					border: @border-radius-base @border-radius-base 0 0;
					border-color:rgba(0,0,0,.09);
					box-shadow: none;
					height: 100%;
					line-height: 30px;
				}
				&:not(.active):not(.disabled) > a:hover{
					background-color: rgba(0,0,0,0.05);
					border-color:rgba(0,0,0,.09);
				}
			}
			> .disabled > a{
				&, &:hover, &:active{
					border-color:rgba(0,0,0,.09);
				}
			}
		}
		.panel-control .pagination > li:not(.active) > a, .pagination > li > a{
			background-color: transparent;
			color: inherit;
		}
		.panel-control .pagination > li > a:hover, .pagination > li > a:focus{
			box-shadow: none;
		}
	}




	/* Panel with pagination */
	/*---------------------------------*/
	& when (@panel-w-btn = true){
		.panel-control .btn,
		.panel-control .dropdown-toggle.btn{
			border:0;
		}
		.panel-control .open > .btn,
		.panel-control .btn.active,
		.panel-control .btn:active{
			box-shadow: none !important
		}
		.panel-control .btn-default{
			background-color: transparent;
			color: inherit
		}
		.panel-control > .btn:first-child{
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
		}
		.panel-control > .btn:last-child,
		.panel-control > .btn-group:last-child > .btn:first-child{
			border-bottom-right-radius: 0
		}
	}
}



